main {
	max-width: 680px;
	width: 100%;
	margin-left: auto;
	margin-right: auto;
	padding: 10px 0;
	
	article {
		padding: 10px;

		#post-title {
			width: 100%;
			text-align: center;
			font-size: 4em;
			margin-bottom: 50px;
		}
		#post-img {
			float: right;
			padding: 10px;
			max-width: 300px;
			min-width: 200px;
			width: 100%;
		}
		#post-date {
			font-size: 2em;
		}
		p {
			font-size: 1.25em;
		}
	}
}
